import './index.scss'
const data = [
    { id: 1, title: 'title 1', url: 'summer-bg.jpg'},
    { id: 2, title: 'title 2', url: 'rainy-bg.jpg'},
    { id: 3, title: 'title 3', url: 'winter-bg.jpg'},
]
const root = document.getElementById('app');

const block  = document.createElement('div');
block.className = 'weather-block'
root.append(block);

const audio = document.createElement('audio');
audio.setAttribute('controls', '');
const source = document.createElement('source');
source.setAttribute('src', './assets/sounds/rain.mp3');
source.setAttribute('type', 'audio/mpeg');
audio.append(source);

root.append(audio)
{/* <audio controls>
            <source src="../src/assets/sounds/rain.mp3" type="audio/mpeg">
          Your browser does not support the audio element.
        </audio> */}

function createContent(item) {
    const div = document.createElement('div')
    div.textContent = item.title
    div.classList.add('weather-block-item')
    const str = 'url(\'./assets/' + item.url + '\')';
    div.style.backgroundImage = str
    block.append(div)
}

data.forEach(createContent)